.catalog-menu {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 5px;
  font-size: 13px;

  & .button,
  a {
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: 5px;
    padding: 10px 0;
    border-radius: 5px;
    background-color: var(--extra-light-gray);

    &:active {
      color: var(--white);
      background-color: var(--basic-green);
      border-radius: 5px;
    }
  }

  @media (min-width: @desktop-width) {
    & h2 {
      display: none;
    }
  }
}
