.selected-properties {
  display: flex;
  flex-wrap: wrap;
  gap: 7px;
  font-size: 13px;

  & ul {
    display: flex;
    flex-wrap: wrap;
    gap: 7px;
    font-weight: 300;

    & li {
      display: flex;
      align-items: center;
      padding-left: 8px;
      border-radius: 5px;
      background-color: var(--extra-light-gray);
    }
  }

  & .button-close {
    width: 27px;
    height: 27px;
    background-image: url(data:image/svg+xml;base64,);
  }

  & > .button {
    padding: 3px 8px 4px 8px;
    border-radius: 5px;
    color: var(--basic-green);
    background-color: var(--extra-light-gray);
  }

  @media (min-width: @desktop-width) {
    & .button-close {
      width: 20px;
      height: 20px;
    }

    & > .button {
      padding: 3px 8px 1px 8px;
    }
  }
}
