*,
::before,
::after {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
p,
dl,
dt,
dd,
figure {
  margin: 0;
}

ul,
ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
  font-style: italic;
  background-repeat: no-repeat;
  background-size: cover;
  // shape-margin: 0.75rem;
}

::placeholder {
  font-size: inherit;
}
